import { Component, OnInit, Input } from '@angular/core';
import { environment } from './../../../../../environments/environment';

@Component({
  selector: 'app-product-option-select',
  templateUrl: './product-option-select.component.html',
  styleUrls: ['./product-option-select.component.scss']
})
export class ProductOptionSelectComponent implements OnInit {
  @Input() gType: any;
  @Input() optionTypes: any;
  @Input() currentSelectedOptions: any;
  @Input() customOptionTypesHash: any;
  type: any;
  control_type: any;

  constructor() { }

  ngOnInit() {
    // console.log(this.type, this.optionTypes, this.currentSelectedOptions);
    this.optionTypes.forEach(optionType => {
      if (optionType.id == this.gType.second_child_id){
        this.type = optionType;
      }
    });

    this.control_type = {};

    for (let key in this.customOptionTypesHash){
      let type = this.customOptionTypesHash[key];
      // console.log("type", key);
      for (let key2 in type){
        // console.log("value", key2);
        if (type[key2].optionValue.id == this.gType.show_option_value ){
          let obj = {};
          obj[key] = key2;
          Object.assign(this.control_type, obj);
        }
      }
    }

    console.log("control type", this.control_type);

    

    console.log("####", this.type);
    console.log("####", this.customOptionTypesHash);
  }
  getProductImageUrl(url) {
    var temp = url + "";
    if (temp.charAt(temp.length - 1) == '/')
      return false;
    else
      return environment.API_ENDPOINT + url;
  }
  
  IsDisabled(){
    for (let key in this.currentSelectedOptions){
      for (let key2 in this.control_type){
        if (key == key2 && this.currentSelectedOptions[key] == this.control_type[key2]){
          return false;
        }
      }
    }
    return true;
  }
}



